*{
    margin: 0px;
    padding: 0px;
    font-family: Helvetica, sans-serif;
    background-color: gainsboro;
}
.nav{
    height: 40px;
    width: calc(100% - 20px);
    background-color:blue;
    display: flex;
    align-items: center;
    color: white;
    padding: 10px;
    justify-content: space-between;
}
.menu-cart{
    width: 33.3%;
    display: flex;
    justify-content: flex-end;
    background-color: blue;
}
.logo{
    width: 33.3%;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    background-color:blue;
    color: white;
}
.icon{
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    background-color:blue;
    color: white;
    margin-left: 15px;
}
h1{
    font-size: 15px;
    background-color:blue;
    color: white;
    height: 20px;
    padding-top: 5px;
}
.menu-desplegable{
    width: 100%;
    height: 150px;
    background-color: blue;
    border-top: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.boton-menu{
    background-color: white;
    color: blue;
    border: none;
    margin: 5px;
    width: 90%;
}
.carrito{
    width: calc(100% - 10px);
    height: 200px;
    background-color: white;
    margin: 5px;
}
.productos{
    width: 100%;
    height: 100%;
    background-color: gainsboro;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.producto{
    height: 230px;
    width: calc(100% - 10px);
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;    
    margin-top: 5px;
}
.foto{
    width: 150px;
    height: 150px;
    margin: 5px;
}
.producto-info{
    height: 140px;
    margin: 5px;
    padding: 5px;
    padding-top: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: white;
}
.subtitulo{
    width: 100%;
    display: flex;
    justify-content: center;
    text-align: center  ;
    padding: 5px;
    background-color: white;
}
.precio{
    width: 90px;
    height: 20px;
    font-size: 15px;
    margin: 10px;
    background-color: white;
    color: blue;
    display: flex;
    justify-content: center;
    align-items: center;
}
.boton{
    color: white;
    border: 0px;
    background-color: blue;
    width: 90px;
    height: 30px;
    margin: 5px;
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.boton-container{
    width: calc(100% - 10px);
    background-color: white;
    margin: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}